import React from "react";
import {Modal, Form, Input, Button, Select} from "antd";
import {connect} from "react-redux";
import {ModalStyle} from "./modal-window-editing.style";
import {onFinish} from "./modal-window-editing.logics";
import {editAccount, setButtonState} from "../../../../actions";

const {Option} = Select;

const textError = '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –Ω–µ –æ—Å—Ç–∞–≤–ª—è–π—Ç–µ –ø–æ–ª–µ –ø—É—Å—Ç—ã–º!'

const InputBox = ({label, name}) => {
    return (
        <Form.Item
            label={label}
            name={name}
            rules={[{
                required: true,
                message: textError
            }]}
        >
            <Input/>
        </Form.Item>
    )
}

const ModalWindowEditing = ({visible, account, changeVisibility, Roles, editAccount, loading_button, setButtonState}) => {

    const {surname, name, midname, login, accountType, role, subject, _id} = account

    return (
        <Modal
            title={'–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –∞–∫–∫–∞—É–Ω—Ç–∞'}
            visible={visible}
            onCancel={changeVisibility}
            cancelButtonProps={{style: {display: 'none'}}}
            okButtonProps={{style: {display: 'none'}}}
            footer={false}
        >
            <ModalStyle>
                <Form
                    name="basic"
                    labelCol={{span: 8}}
                    wrapperCol={{span: 16}}
                    initialValues={{
                        surname,
                        name,
                        midname,
                        login,
                        accountType,
                        subject,
                        role: role ? role._id : ''
                    }}
                    onFinish={(values) => {
                        onFinish(values, _id, account, setButtonState, editAccount, changeVisibility)
                    }}
                    autoComplete="off"
                >
                    <InputBox label={'–§–∞–º–∏–ª–∏—è'} name={'surname'}/>
                    <InputBox label={'–ò–º—è'} name={'name'}/>
                    <InputBox label={'–û—Ç—á–µ—Å—Ç–≤–æ'} name={'midname'}/>
                    <InputBox label={'Login'} name={'login'}/>
                    <Form.Item
                        name="accountType"
                        label="–¢–∏–ø –∞–∫–∫–∞—É–Ω—Ç–∞"
                        rules={[{
                            required: true,
                            message: textError
                        }]}
                    >
                        <Select
                            placeholder="–ß–µ–º –∑–∞–Ω–∏–º–∞–µ—Ç—Å—è —Å–æ—Ç—Ä—É–¥–Ω–∏–∫?"
                            allowClear
                        >
                            <Option value="admin">–ê–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä</Option>
                            <Option value="manager">–ú–µ–Ω–µ–¥–∂–µ—Ä</Option>
                            <Option value="teacher">–£—á–∏—Ç–µ–ª—å</Option>
                            <Option value="senior-teacher">–°—Ç–∞—Ä—à–∏–π –ü—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª—å</Option>
                            <Option value="partner">–ü–∞—Ä—Ç–Ω–µ—Ä</Option>
                            <Option value="support">–ü–æ–¥–¥–µ—Ä–∂–∫–∞</Option>
                        </Select>
                    </Form.Item>
                    <Form.Item
                        name="role"
                        label="–†–æ–ª—å"
                        rules={[{
                            required: true,
                            message: textError
                        }]}
                    >
                        <Select
                            placeholder="–ö–∞–∫–∏–µ –ø—Ä–∞–≤–∞ —É —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–∞?"
                            allowClear
                        >
                            {
                                Roles.map((role) => (
                                    <Option value={role._id} key={role._id}>{role.name}</Option>
                                ))
                            }
                        </Select>
                    </Form.Item>
                    <Form.Item
                        style={{
                            display: accountType === 'teacher' ? '' : 'none'
                        }}
                        name="subject"
                        label="–ü—Ä–µ–¥–º–µ—Ç"
                        rules={[{
                            required: accountType === 'teacher',
                            message: textError
                        }]}
                    >
                        <Select
                            placeholder="–ï—Å–ª–∏ —Ä–µ–¥–∞–∫—Ç–∏—Ä—É–µ—Ç–µ —É—á–∏—Ç–µ–ª—è..."
                            allowClear
                        >
                            <Option value="–ü—Ä–æ–≥—Ä–∞–º–º–∏—Ä–æ–≤–∞–Ω–∏–µ">–ü—Ä–æ–≥—Ä–∞–º–º–∏—Ä–æ–≤–∞–Ω–∏–µ</Option>
                            <Option value="–î–∏–∑–∞–π–Ω">–î–∏–∑–∞–π–Ω</Option>
                        </Select>
                    </Form.Item>

                    <Form.Item className={'submit'} wrapperCol={{offset: 8, span: 16}}>
                        <Button
                            style={{
                                marginRight: '10px'
                            }}
                            onClick={changeVisibility}
                        >
                            –ó–∞–∫—Ä—ã—Ç—å
                        </Button>
                        <Button
                            type="primary"
                            htmlType="submit"
                            loading={loading_button}
                        >
                            –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
                        </Button>
                    </Form.Item>
                </Form>
            </ModalStyle>
        </Modal>
    )
}

const mapStateToProps = (state) => {
    return {
        Roles: state.roles,
        loading_button: state.loading_button
    }
}

const mapDispathToProps = {
    editAccount,
    setButtonState
}

export default connect(mapStateToProps, mapDispathToProps)(ModalWindowEditing)