import axios from "axios";
import Url from "../../../../url/url";
import errorHandler from "../../../error-handler/error-handler";
import {Toast} from "../../../../alert/alert";

const edit_account = (id, data, onSuccess) => {
    axios({
        method: 'PATCH',
        url: `${Url}/AdminPanel/CRMAccounts/${id}`,
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        data
    })
        .then((res) => {
            onSuccess(res.data)
        })
        .catch((error) => {
            errorHandler(edit_account, error)
        })
}

const onFinish = (values, _id, account, setButtonState, editAccount, changeVisibility) => {
    setButtonState()
    edit_account(_id, values, (account) => {
        editAccount(account)
        Toast.fire({
            icon: 'success',
            title: '–ò–∑–º–µ–Ω–µ–Ω–∏—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω—ã'
        })
        changeVisibility()
        setButtonState()
    })
};

export {
    edit_account,
    onFinish
}