import {actionPermissionsGet, dataPermissionsGet} from "./get-possibilities/get-possibilities";
import {Box} from "./all-employees.style";
import {Input, Tooltip} from "antd";
import React from "react";
import WorkTime from "./work-time/work-time";
import AccountEditing from "./account-editing/account-editing";

export const Columns = () => (
    [
        {
            key: 'surname',
            dataIndex: 'surname',
            title: '–§–∞–º–∏–ª–∏—è'
        },
        {
            key: 'name',
            dataIndex: 'name',
            title: '–ò–º—è'
        },
        {
            key: 'midname',
            dataIndex: 'midname',
            title: '–û—Ç—á–µ—Å—Ç–≤–æ'
        },
        {
            key: 'login',
            dataIndex: 'login',
            title: '–õ–æ–≥–∏–Ω'
        },
        {
            key: 'work_time_base',
            dataIndex: 'work_time_base',
            title: '–¢–∏–ø –∞–∫–∫–∞—É–Ω—Ç–∞',
            render: (work_time_base) => {
                if (work_time_base.accountType !== 'teacher') {
                    return (
                        <p>{work_time_base.accountType}</p>
                    )
                } else {
                    return (
                        <WorkTime title={work_time_base.accountType} id={work_time_base._id} initial_workHours={work_time_base.workHours}/>
                    )
                }
            }
        },
        {
            key: 'role',
            dataIndex: 'role',
            title: '–†–æ–ª—å',
            render: (role) => {

                if (role) {
                    let {name, actionPermissions, dataPermissions} = role

                    let title = [...actionPermissionsGet(actionPermissions), ...dataPermissionsGet(dataPermissions)]

                    return (
                        <Box>
                            <span className="badge bg-info text-dark">{name}:</span>
                            <Tooltip placement="leftTop" title={title.join(", ")}>
                                <Input
                                    placeholder={title.join(", ")}
                                    readOnly='readonly'
                                />
                            </Tooltip>
                        </Box>
                    )
                }
            }
        },
        {
            key: 'account_editing',
            dataIndex: 'account_editing',
            title: '',
            render: (account_editing) => (<AccountEditing account={account_editing}/>)
        }
    ]
)